import PerfectScrollbar from "react-perfect-scrollbar";
import {
  Box,
  Card,
  CardHeader,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from "@material-ui/core";

const OverallTable = (props) => (
  <Card {...props}>
    <CardHeader title={props.title} />
    <Divider />
    <PerfectScrollbar>
      <Box sx={{ minWidth: props.minWidth }}>
        <Table>
          <TableHead>
            <TableRow>
              {props.headers.map((header) => (
                <TableCell>{header}</TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {props.data.map((row) => {
              return (
                <TableRow hover key={row[props.headers[0]]}>
                  {Object.keys(row).map((h) => {
                    return <TableCell>{row[h]}</TableCell>;
                  })}
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </Box>
    </PerfectScrollbar>
  </Card>
);

export default OverallTable;
