const storeUser = (user) => {
  try {
    localStorage.setItem("user", user);
  } catch (error) {
    console.error(error);
  }
};

const getUser = async () => {
  try {
    return JSON.parse(localStorage.getItem("user"));
  } catch (error) {
    console.error(error);
  }
};

const removeUser = () => {
  try {
    localStorage.removeItem("user");
  } catch (error) {
    console.error(error);
  }
};

const authStorage = { storeUser, getUser, removeUser };

export default authStorage;
