const { default: axios } = require("axios");

const getUser = () => {
  return axios.get("/user");
};

const getUserBalance = () => {
  return axios.get("/user/balance");
};

const getUserDeposits = () => {
  return axios.get("/user/deposits");
};

const getUserContributions = () => {
  return axios.get("/user/contributions");
};

const getAllMembersMonthlyContributions = () => {
  return axios.get("/user/allcontributions");
};

const changePassword = (password) => {
  return axios.post("/user/changepassword", { password });
};

module.exports = {
  getUser,
  getUserBalance,
  getUserDeposits,
  getUserContributions,
  getAllMembersMonthlyContributions,
  changePassword,
};
