import {
  Avatar,
  Card,
  CardContent,
  Grid,
  Box,
  Typography,
} from "@material-ui/core";
import { indigo, red } from "@material-ui/core/colors";
import CurrentFormatter from "../../util/CurrencyFormatter";

const AmountCard = (props) => (
  <Card sx={{ height: "100%" }} {...props}>
    <CardContent>
      <Grid container spacing={3} sx={{ justifyContent: "space-between" }}>
        <Grid item>
          <Typography color="textSecondary" gutterBottom variant="h6">
            {props.description}
          </Typography>
          <Typography color="textPrimary" variant="h3">
            {CurrentFormatter.format(props.amount)}
          </Typography>
        </Grid>
        <Grid item>
          <Avatar
            sx={{
              backgroundColor: indigo[600],
              height: 56,
              width: 56,
            }}
          >
            {props.children}
          </Avatar>
        </Grid>
      </Grid>
      {props.target && (
        <Box
          sx={{
            pt: 2,
            display: "flex",
            alignItems: "center",
          }}
        >
          <Typography
            sx={{
              color: red[900],
              mr: 1,
            }}
            variant="body2"
          >
            {parseInt((props.amount * 100) / props.target)}%
          </Typography>
          <Typography color="textSecondary" variant="caption">
            {props.targetDescription}
          </Typography>
        </Box>
      )}
    </CardContent>
  </Card>
);

export default AmountCard;
