import { useEffect, useState } from "react";
import { Navigate } from "react-router-dom";
import { Helmet } from "react-helmet";
import { Box, Container, Grid, colors } from "@material-ui/core";
import AttachMoneyIcon from "@material-ui/icons/AttachMoney";
import AccountBalanceIcon from "@material-ui/icons/AccountBalance";
import MoneyIcon from "@material-ui/icons/Money";

import AmountCard from "../components/dashboard/AmountCard";
import ProgressCard from "../components/dashboard/ProgressCard";
import BarChart from "../components/dashboard/BarChart";
import OverallTable from "../components/dashboard/Table";

import userApi from "../api/user";
import useAuth from "../auth/useAuth";

import CurrencyFormatter from "../util/CurrencyFormatter";

const Dashboard = () => {
  const { user } = useAuth();
  const [balance, setBalance] = useState({
    "Opening Balance": 50000,
    Contributions: 0,
    "Closing Balance": 0,
    SemesterContribution: 0,
  });

  const [contributionsData, setContributionsData] = useState(null);
  const [overallContributions, setOverallContributions] = useState([]);
  const [headers, setHeaders] = useState([]);

  const getBalance = async () => {
    try {
      let response = await userApi.getUserBalance();
      setBalance(response.data);
    } catch (e) {
      console.error(e);
    }
  };

  const getContributionsBreakdown = async () => {
    try {
      let response = await userApi.getUserContributions();
      let contributions = response.data;
      let amounts = [];
      let targets = [];
      Object.keys(contributions).forEach((c) => {
        amounts.push(contributions[c]["amount"]);
        targets.push(contributions[c]["target"]);
      });

      const data = {
        datasets: [
          {
            backgroundColor: colors.indigo[500],
            data: amounts,
            label: "Amount",
          },
          {
            backgroundColor: colors.grey[200],
            data: targets,
            label: "Target",
          },
        ],
        labels: Object.keys(response.data),
      };

      setContributionsData(data);
    } catch (error) {
      console.error(error);
    }
  };

  const getDeposits = async () => {
    try {
      let response = await userApi.getUserDeposits();
    } catch (error) {
      console.error(error);
    }
  };

  const getOverallContributions = async () => {
    try {
      let response = await userApi.getAllMembersMonthlyContributions();
      let data = response.data.map((row) => ({
        Month: row["Month"],
        Amount: CurrencyFormatter.format(row["Amount"]),
      }));
      let headers = Object.keys(data[0]);
      setHeaders(headers);
      setOverallContributions(data);
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    getBalance();
    //getDeposits();
    getContributionsBreakdown();
    getOverallContributions();
  }, []);

  return user ? (
    <>
      <Helmet>
        <title>Dashboard | Simunye</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: "background.default",
          minHeight: "100%",
          py: 3,
        }}
      >
        <Container maxWidth={false}>
          <Grid container spacing={3}>
            <Grid item lg={3} sm={6} xl={3} xs={12}>
              <AmountCard
                description="TOTAL CONTRIBUTIONS"
                amount={balance["Contributions"]}
              >
                <MoneyIcon />
              </AmountCard>
            </Grid>
            <Grid item lg={3} sm={6} xl={3} xs={12}>
              <AmountCard
                description="SEMESTER CONTRIBUTION"
                amount={balance["SemesterContribution"]}
                target={balance["Opening Balance"] / 3 / 3}
                targetDescription="Towards Semester Target"
              >
                <AttachMoneyIcon />
              </AmountCard>
            </Grid>
            <Grid item lg={3} sm={6} xl={3} xs={12}>
              <ProgressCard
                description="GOAL"
                percentage={
                  (balance["Contributions"] * 100) / balance["Opening Balance"]
                }
              />
            </Grid>
            <Grid item lg={3} sm={6} xl={3} xs={12}>
              <AmountCard
                description="CLOSING BALANCE"
                amount={balance["Closing Balance"]}
              >
                <AccountBalanceIcon />
              </AmountCard>
            </Grid>
            <Grid item lg={8} md={12} xl={9} xs={12}>
              {contributionsData && (
                <BarChart
                  data={contributionsData}
                  title={"Monthly Contributions Breakdown"}
                  description={`LAST ${new Date().getMonth() + 1} MONTH(S)`}
                />
              )}
            </Grid>
            <Grid item lg={4} md={6} xl={3} xs={12}>
              {overallContributions.length > 0 && (
                <OverallTable
                  headers={headers}
                  data={overallContributions}
                  title={"Monthly Contributions"}
                  minWidth={200}
                />
              )}
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  ) : (
    <Navigate to="/login" />
  );
};

export default Dashboard;
